<template>
  <div class="liveDetail">
    <div class="mescroll" id="list">
      <div id="listData">
        <div class="top">
          <div class="banner">
            <img :src="detail.aimg?$utils.dealImg(detail.aimg):require('../../assets/img/default/defaultH.png')" alt="">
          </div>
          <div class="content">
            <div class="flex">
              <p class="name">{{detail.aname}}</p>
              <img class="icon" @click="toLive" src="../../assets/img/icon/full.png" alt="">
            </div>
            <div class="flex">
              <div class="text1">{{detail.aoutPrice?`${detail.asalenum}人开通`:`${detail.avisitnum}人浏览`}}</div>
              <div class="text2">{{detail.aoutPrice?'￥'+detail.aoutPrice.toFixed(2):'免费'}}</div>
            </div>
          </div>
        </div>
        <div class="mid">
          <van-tabs v-model="active" :line-width="20" :line-height="3" :sticky="true" :swipe-threshold="6" :border="false" :title-active-color="'#000'" @click="onClick">
            <van-tab title="详情">
              <div class="detail">
                <div v-html="detail.amsg"></div>
              </div>
            </van-tab>
            <van-tab title="评论">
              <div class="comment">
                <div class="comment-top">
                  <img :src="detail.shopHeader?$utils.dealImg(detail.shopHeader):require('../../assets/img/default/userDefault.png')" alt />
                  <div class="right">
                    <p class="name">{{detail.shopName}}</p>
                    <div class="content">
                      <p>欢迎进入直播间：</p>
                      <p>1、请自行调节手机音量至合适的状态。</p>
                    </div>
                  </div>
                </div>
                <div class="list">
                  <div class="item" v-for="(item,index) in evaluateList" :key="index">
                    <div class="left">
                      <img :src="item.userHeader?$utils.dealImg(item.userHeader):require('../../assets/img/default/userDefault.png')" alt />
                    </div>
                    <div class="right">
                      <div class="name">{{item.userName}}</div>
                      <div class="info">
                        <span class="time">{{getTimeText(item.createDate)}}</span>
                        <span class="del" v-if="item.userCode == userCode" @click="del(item.dcode)">删除</span>
                      </div>
                      <div class="message">{{item.dmsg}}</div>
                    </div>
                  </div>
                </div>
              </div>
            </van-tab>
          </van-tabs>
        </div>
      </div>
    </div>
    <div class="bottom">
      <van-tabbar v-model="active1" active-color="#000" inactive-color="#000">
        <van-tabbar-item @click="show = true">
          <span>写评论</span>
          <img slot="icon" src="../../assets/img/college/icon6.png" />
        </van-tabbar-item>
        <van-tabbar-item @click="moreShow = true">
          <span>更多</span>
          <img slot="icon" src="../../assets/img/college/icon7.png" />
        </van-tabbar-item>
      </van-tabbar>
    </div>
    <div class="borderRad">
      <van-popup v-model="moreShow" position="bottom">
        <div class="popup" @click="moreShow = false">
          <div class="popup-top">
            <p class="text">更多</p>
            <p class="icon">×</p>
          </div>
          <div class="popup-mid">
            <div class="list">
              <div class="item" @click="toIndex">
                <img src="../../assets/img/college/icon8.png" alt />
                <p>首页</p>
              </div>
              <div class="item" @click="shareShow = true">
                <img src="../../assets/img/college/icon9.png" alt />
                <p>分享好友</p>
              </div>
              <div class="item" @click="collect">
                <img src="../../assets/img/college/icon10.png" alt />
                <p>收藏</p>
              </div>
              <div class="item">
                <img src="../../assets/img/college/icon11.png" alt />
                <p>投诉</p>
              </div>
            </div>
          </div>
        </div>
      </van-popup>
    </div>
    <van-popup v-model="show" position="bottom">
      <div class="popup">
        <div class="flex">
          <div class="left">
            <van-field v-model="message" placeholder="发评论" />
          </div>
          <div class="btn" @click="send">发送</div>
        </div>
      </div>
    </van-popup>
    <div class="mask" v-if="shareShow" @click="shareShow=false">
      <img src="../../assets/img/activity/shang.png" alt="">
    </div>
  </div>
</template>
<script>
import { Tab, Tabs, Tabbar, TabbarItem, Popup, Field, Dialog } from "vant";
import "videojs-flash"; // 引入才能播放rtmp视频
import "videojs-contrib-hls"; // 引入才能播放m3u8文件
import { videoPlayer } from "vue-video-player";
import {
  getLiveDetail,
  collectSave,
  getSectionList,
  getSectionRecommendList,
  getEvaluateList,
  wxApi,
  saveEvaluate,
  delEvaluate,
} from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem,
    [Popup.name]: Popup,
    [Field.name]: Field,
    videoPlayer,
  },
  data() {
    return {
      active: 0,
      active1: 0,
      id: "",
      moreShow: false,
      show: false,
      message: "",
      detail: {},
      mescroll: null,
      evaluateList: [],
      Interval: null,
      time: 0,
      shareShow: false,
      userCode: "",
    };
  },
  methods: {
    onClick() {
      if (this.mescroll) {
        this.mescroll.scrollTo(0, 0);
        this.mescroll.resetUpScroll();
      } else {
        this.$nextTick(() => {
          this.startMescroll();
        });
      }
    },
    toLive() {
      if (this.detail.aliveStatus == "1") {
        this.$toast("主播还未开播");
      } else if (this.detail.aliveStatus == "2") {
        this.$router.push({
          name: "liveIndex",
          query: { url: encodeURIComponent(this.detail.aliveUrlView) },
        });
      } else if (this.detail.aliveStatus == "4") {
        this.$toast("直播已结束");
      } else {
      }
    },
    toIndex() {
      this.$router.push({
        name: "collegeIndex",
      });
    },
    collect() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      collectSave({ learnCode: this.detail.acode, favType: "1" }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast("收藏成功");
        } else {
          this.$toast.fail(res.errorMsg);
        }
      });
    },
    send() {
      if (!this.message) {
        this.$toast("请输入评价");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      saveEvaluate({
        acode: this.detail.acode,
        dmsg: this.message,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          if (this.active == 1) {
            this.mescroll.resetUpScroll();
          }
          this.$toast("评价成功");
          this.show = false;
        } else {
          this.$toast(res.msg);
        }
      });
    },
    del(dcode) {
      Dialog.confirm({
        title: "提示",
        message: "是否删除此条评价？",
      })
        .then(() => {
          // on confirm
          const loading = this.$toast.loading({
            duration: 0, // 持续展示 toast
            forbidClick: true, // 禁用背景点击
          });
          delEvaluate({
            dcode: dcode,
          }).then(res => {
            loading.clear();
            if (res.code == 0) {
              this.$toast("删除成功");
              this.mescroll.resetUpScroll();
            } else {
              this.$toast(res.msg);
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    },

    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getLiveDetail({ aCode: this.$route.query.acode }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.detail = res.data;
          this.detail.amsg = this.detail.amsg.replace(
            /(<img[\s\S]+?)src=(['"])/g,
            '<img src="' + this.$config.http
          );
        } else {
          this.$toast.fail(res.errorMsg);
        }
      });
    },
    getList(pageNum, pageSize, successCallback, errorCallback) {
      let dataObj = {};
      if (this.active == 1) {
        dataObj = {
          acode: this.detail.acode,
          pageNum: pageNum,
          pageSize: pageSize,
        };
        getEvaluateList(dataObj)
          .then(res => {
            if (res.code == 0) {
              successCallback && successCallback(res.data, "evaluateList");
            } else {
              $this.$toast.fail(res.errorMsg);
            }
          })
          .catch(res => {
            errorCallback && errorCallback();
            console.log(res);
          });
      } else {
        console.log(111);
      }
    },
    startMescroll() {
      this.mescroll = new MeScroll(`list`, {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: `listData`,
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据",
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true, // 是否开启懒加载,默认false
          },
        },
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.getList(
        page.num,
        page.size,
        function(curPageData, name) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData, name);
          if (page.num == 1) $this[name] = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this[name].push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    },
    share() {
      let that = this;
      wxApi()
        .then(res => {
          console.log(this.$wx, res.data);
          this.$wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"],
          });
          this.$wx.ready(function() {
            that.$wx.onMenuShareAppMessage({
              title: that.detail.dname, // 分享标题
              desc: that.detail.dname2, // 分享描述
              link:
                that.$config.http +
                "/wx/#/liveDetail?acode=" +
                that.detail.dcode, // 分享链接
              imgUrl: that.$utils.dealImg(that.detail.dimg), // 分享图标
              type: "link", // 分享类型,music、video或link，不填默认为link
              dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
              success: function() {
                // 用户确认分享后执行的回调函数
                that.shareShow = false;
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
            that.$wx.onMenuShareTimeline({
              title: that.detail.dname, // 分享标题
              link:
                that.$config.http +
                "/wx/#/liveDetail?acode=" +
                that.detail.dcode, // 分享链接
              imgUrl: that.$utils.dealImg(that.detail.dimg), // 分享图标
              success: function() {
                that.shareShow = false;
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
    getTimeText(date) {
      var today = new Date();
      today.setHours(0);
      today.setMinutes(0);
      today.setSeconds(0);
      today.setMilliseconds(0);
      //给出时间 - 今天0点
      var offset = new Date(date).getTime() - today.getTime(),
        offset1 = new Date().getTime() - new Date(date).getTime();
      var isToday = offset / 1000 / 60 / 60,
        isToday1 = offset1 / 1000 / 60 / 60;
      if (isToday1 > 0 && isToday1 <= 1) {
        return "刚刚";
      } else if (isToday > 0 && isToday <= 24) {
        return "今天";
      } else if (isToday < 0 && isToday <= -24) {
        return "昨天";
      } else {
        return date;
      }
    },
  },
  mounted() {
    document.title = "直播详情";
    this.userCode = sessionStorage.getItem("userId");
    this.id = sessionStorage.getItem("directoryId")
      ? sessionStorage.getItem("directoryId")
      : this.$route.query.acode;
    this.loadData();
    this.share();
  },
};
</script>
<style lang="scss" scoped>
.liveDetail {
  height: calc(100vh - 50px);
  .top {
    .banner {
      width: 100vw;
      display: block;
      img {
        width: 100%;
      }
    }
    .content {
      background: #fff;
      padding: 10px;
      .flex {
        margin: 5px 0 0;
        display: flex;
        align-items: center;
        .name {
          font-size: 15px;
          font-weight: bold;
          line-height: 25px;
          flex: 2;
        }
        .icon {
          width: 25px;
        }
        .text1 {
          color: #999;
          font-size: 13px;
          flex: 2;
        }
        .text2 {
          flex: 1;
          text-align: right;
          color: #9e1435;
          font-size: 13px;
        }
      }
    }
  }
  .mid {
    background: #fff;
    .detail {
      padding: 10px;
      img {
        width: 100%;
      }
    }
    .comment {
      padding: 10px;
      .comment-top {
        display: flex;
        padding: 10px 0;
        background: #fff;
        margin: 0 0 10px;
        img {
          width: 30px;
          height: 30px;
          border-radius: 50%;
          margin-right: 5px;
        }
        .right {
          .name {
            font-size: 13px;
            line-height: 30px;
            color: #666;
          }
          .content {
            font-size: 15px;
            line-height: 25px;
          }
        }
      }
      .list {
        .item {
          border-bottom: 1pt solid #efefef;
          display: flex;
          margin: 0 0 10px;
          .left {
            width: 60px;
            margin-right: 10px;
            img {
              width: 60px;
              height: 60px;
              border-radius: 50%;
            }
          }
          .right {
            flex: 2;
            .name {
              width: calc(100vw - 20px - 60px - 10px);
              line-height: 30px;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            .info {
              .time {
                font-size: 13px;
                line-height: 30px;
                margin-right: 20px;
              }
              .del {
                font-size: 13px;
                line-height: 30px;
                margin-right: 10px;
                color: #626b7c;
              }
            }
            .message {
              font-size: 15px;
              line-height: 30px;
            }
          }
        }
      }
    }
    .recommend {
      padding: 10px;
      .title {
        font-weight: bold;
        border-left: 2px solid #f00;
        padding-left: 10px;
        line-height: 20px;
        margin: 5px 0;
      }
      .list {
        .item {
          border-bottom: 1pt solid #efefef;
          margin-bottom: 10px;
          padding: 5px 0;
          display: flex;
          .left {
            img {
              width: 120px;
              margin-right: 5px;
            }
          }
          .right {
            position: relative;
            flex: 2;
            .name {
              font-size: 13px;
              line-height: 20px;
              max-height: 50px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
            .item-bottom {
              position: absolute;
              bottom: 0;
              left: 0;
              display: flex;
              width: 100%;
              line-height: 30px;
              .text1 {
                color: #999;
                font-size: 13px;
                flex: 2;
              }
              .text2 {
                flex: 1;
                text-align: right;
                color: #9e1435;
                font-size: 13px;
              }
            }
          }
        }
      }
    }
  }
  .popup {
    &-top {
      text-align: center;
      line-height: 40px;
      position: relative;
      .text {
        // font-weight: bold;
      }
      .icon {
        position: absolute;
        top: 0;
        right: 10px;
        font-size: 20px;
        color: #999;
      }
    }
    &-mid {
      .list {
        display: flex;
        align-items: center;
        padding: 30px 0 40px;
        .item {
          text-align: center;
          flex: 1;
          img {
            width: 50px;
          }
          p {
            font-size: 13px;
            color: #666;
          }
        }
      }
    }
    .flex {
      display: flex;
      align-items: center;
      padding: 10px;
      .left {
        flex: 2;
        border: 1pt solid #efefef;
        box-sizing: border-box;
        border-radius: 22px;
        margin-right: 10px;
        .van-field {
          border-radius: 22px;
          padding: 5px 10px;
        }
      }
      .btn {
        width: 75px;
        background: #ff2c2b;
        color: #fff;
        line-height: 34px;
        border-radius: 22px;
        text-align: center;
        font-size: 13px;
      }
    }
  }
  .mask {
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    img {
      position: fixed;
      top: 10px;
      right: 10px;
      width: 100px;
    }
  }
}
</style>
<style lang="scss">
.liveDetail {
  .van-tabs__wrap {
    border-bottom: 1pt solid #e0e0e0;
  }
  .mescroll-empty {
    transform: translateY(-5%);
    img {
      width: 120px;
    }
  }
  .borderRad {
    .van-popup {
      border-radius: 10px 10px 0 0;
    }
  }
}
</style>